body {
  color: #202020;
  font-family: New_Channel_Font, sans-serif;
  font-weight: 500;
  margin: 0;
  min-width: 320px;
  padding: 0; }

* {
  box-sizing: border-box;
  outline: none !important; }

::selection {
  background: #0db1f4; }

.container {
