a,
a:hover {
  text-decoration: none; }

a:hover {
  color: #0096d3; }

.btn {
  background: #0db1f4;
  border: none;
  border-radius: 6px;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: 18px;
  line-height: 1;
  margin: 15px 0;
  padding: 12px 30px 13px;
  text-align: center;
  text-decoration: none;
  text-transform: none;
  transition: all .3s ease;
  white-space: nowrap; }

.btn:focus,
.btn:hover {
  background: #0096d3;
  color: #fff; }

.btn--small {
  font-size: 16px;
  margin: 0;
  padding: 10px 0;
  width: 100%; }

@media only screen and (max-width: 767px) {
