    font-size: 12px; } }

.btn_gray {
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 3px;
  color: #999;
  cursor: pointer;
  display: inline-block;
  font-size: 16px;
  line-height: 1;
  margin: 15px 0;
  padding: 12px 30px 13px;
  text-align: center;
